import { ActionsTypes } from '../../types/reducers-types';

const ADD_MESSAGE = 'ADD-MESSAGE';

interface UserNameTypes {
  id: number;
  name: string;
}

interface MessageDataTypes {
  id: number;
  message: string;
  user?: string;
}

let initialState = {
  users: [
    { id: 1, name: '' },
    { id: 2, name: '' },
    { id: 3, name: '' },
  ] as Array<UserNameTypes>,
  message: [
    {
      id: 1,
      message: ' ',
    },
    {
      id: 2,
      message:
        '        ,   .     react?    ,  ,      ',
      user: 'me',
    },

    { id: 3, message: ' ' },
  ] as Array<MessageDataTypes>,
};

export type initialStateTypes = typeof initialState;

const dialogsReducer = (
  state = initialState,
  action: ActionsTypes<typeof DialogActions>,
): initialStateTypes => {
  switch (action.type) {
    case ADD_MESSAGE:
      //   bll   
      if (action.messageText) {
        const newMessage = {
          id: 5,
          message: action.messageText,
          user: 'me',
          date: new Date(),
        };
        return { ...state, message: [...state.message, newMessage] };
      }
      return state;
    default:
      return state;
  }
};

export const DialogActions = {
  addMessage: (messageText: string) =>
    ({
      type: ADD_MESSAGE,
      messageText,
    } as const),
};

export default dialogsReducer;
